form {
    position: relative;

    background-color: $variant-black1;
    color: $colorS;
    font-family: $primaryFont;
    box-shadow: 3px 3px 3px rgba(0,0,0,0.5);

    min-height: 70vh;
    max-height: auto;
    min-width: 45vh;
    max-width: auto;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    padding: 15vh 0;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        height: 1vh;
        width: 100%;
        background: linear-gradient(45deg, $secondary-l, $primary-n); }

    &:after {
        content: "";
        position: absolute;
        bottom: 0;
        height: 1vh;
        width: 100%;
        background: linear-gradient(45deg, $secondary-l, $primary-n); }

    #titleForm {
        font-size: 5vh; }

    button {
        border: 2px solid $secondary-d;
        height: 6vh;
        width: 50%;
        border-radius: 25px;
        background-color: rgba(250,250,250,0);
        color: $secondary-d;
        font-family: "Strait", sans-serif;
        font-size: 2.5vh;
        transition: background-color 0.3s;

        &:hover {
            background-color: $secondary-d;
            color: $colorS; }
        &:focus {
            outline: none; } } }


#inputs {
    margin: 6vh 0;
    width: 100%;

    input {
        display: block;
        width: 100%;
        height: 5.5vh;
        margin: 0.5vh 0;

        border: none;

        background: $colorS;
        color: $secondary-n;
        text-align: center;
        font-family: "Strait", sans-serif;
        font-size: 3vh;

        &:focus {
            background: linear-gradient(-45deg, $primary-n 10%, $colorS 20%, $colorS 80%, $secondary-l 100%);
            color: $secondary-d;
            outline: none; } } }

